import React, { useContext, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableCell,
  TableFooter,
  TableContainer,
  Button,
  Card,
  CardBody,
  Pagination,
  Input,
  Select,
} from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { FiPlus } from "react-icons/fi";
import { Link } from "react-router-dom";

import useAsync from "hooks/useAsync";
import useToggleDrawer from "hooks/useToggleDrawer";
import UploadManyTwo from "components/common/UploadManyTwo";
import NotFound from "components/table/NotFound";
import ProductServices from "services/ProductServices";
import PageTitle from "components/Typography/PageTitle";
import { SidebarContext } from "context/SidebarContext";
import guestsdataformlocal from "../../utils/guests";
import ProductTable from "components/guests/guests";
import MainDrawer from "components/drawer/MainDrawer";
import ProductDrawer from "components/guests/AddGuests";
import AddOnBillDrawer from "components/AddOnBills/CreateAddOnBills";
import useProductFilter from "hooks/useFilter";
import TableLoading from "components/preloader/TableLoading";
import { AddOnLists, GuestbookingDetailsList } from "services/Api";
import { BsFileEarmarkMedical } from "react-icons/bs";
import exportFromJSON from "export-from-json";

const Products = () => {
  const {
    title,
    allId,
    serviceId,
    handleDeleteMany,
    handleUpdateMany,
    handleUpdate,
    handleAdd,
    bookingUser,
    bookingId,
  } = useToggleDrawer();
  const [pagenumber, setpagenumber] = useState(1);
  const [searchname, setSearchname] = useState("");
  const [StartDate, setStartDate] = useState("");
  const [EndDate, setEndDate] = useState("");
  const [expo, setexpo] = useState(false);
  const [loadingExport, setLoadingExport] = useState({
    name: "",
    status: false,
  });
  const [BookingList, setBookingList] = useState([]);
  const [loading, setLoading] = useState([]);
  const { t } = useTranslation();
  const { lang, currentPage, searchText, category, sortedField, limitData } =
    useContext(SidebarContext);

  const { data } = useAsync(() =>
    ProductServices.getAllProducts({
      page: currentPage,
      limit: limitData,
      category: category,
      title: searchText,
      price: sortedField,
    })
  );

  console.log("productss", guestsdataformlocal);
  const {
    serviceData,
    userRef,
    handleSelectFile,
    handleSubmitForAll,
    handleUploadMultiple,
    handleRemoveSelectFile,
  } = useProductFilter(guestsdataformlocal);
  const [wholeCount, setwholeCount] = useState({});

  useEffect(() => {
    getAllBookingList(pagenumber, searchname, StartDate, EndDate);
  }, []);

  // const {
  //   setRole,
  //   totalResults,
  //   resultsPerPage,
  //   handleSubmitUser,
  // } = useFilter(staffFullData);
  const getAllBookingList = async (
    pagenumber,
    searchname,
    StartDate,
    EndDate,
    expo
  ) => {
    const Guest = await GuestbookingDetailsList(
      pagenumber,
      searchname,
      StartDate,
      EndDate,
      expo
    );
    console.log(Guest.data.data, "guest");
    setLoading(false);
    setBookingList(Guest?.data?.data);
    setwholeCount(Guest?.data?.data?.pagination);
  };
  const handleChangePagenumber = (e) => {
    console.log("clicked", e);
    setpagenumber(e);
    getAllBookingList(e, searchname, StartDate, EndDate, expo);
  };
  const handleChangePage = (e) => {
    console.log("clicked food", e);
    setpagenumber(e);
    getAllBookingList(e, searchname);
  };
  const handleSearch = (search) => {
    getAllBookingList(pagenumber, search, StartDate, EndDate, expo);
    console.log(search, "search");
  };
  const setDateFillter = (e) => {
    setStartDate(e.target.value);
    getAllBookingList(pagenumber, searchname, e.target.value, EndDate, expo);
    console.log(e.target.value, "date");
  };
  const setEndDateFillter = (e) => {
    setEndDate(e.target.value);
    getAllBookingList(pagenumber, searchname, StartDate, e.target.value, expo);
    console.log(e.target.value, "date");
  };
  const handleExportCSV = async () => {
    setexpo("true");
    const today = new Date().toISOString().slice(0, 10);
    setLoadingExport({ name: "csv", status: true });
      const guestbookingDetailsList = await GuestbookingDetailsList(
      pagenumber,
      searchname,
      StartDate,
      EndDate,
      expo
    );
    console.log(
        guestbookingDetailsList.data.data?.Bookings,
      "guestbookingDetailsList"
    );

      const guestBookingListCSV = guestbookingDetailsList.data.data?.Bookings.map((item, index) => {
          return { 
              SlNo: index + 1, 
              PropertyName: item.Booking_property.name,
              CustomerName: item.User.name,
              EmailID: item.User.email,
              PhoneNo: item.User.mobile,
              PackageName: item.Booking_package?.name,
              AccommodationQty: "TODO",
              CheckinDate: item.check_in_date,
              CheckoutDate: item.check_out_date,
              NoOfAdults: item.no_of_adults,
              NoOfKids: item.no_of_children,
              PricePerPerson: "TODO",
              PricePerKid: "TODO",
              ALaCarte: "TODO",
              PaidActivities: "TODO",
              Others: item.Add_on_bill?.total_amount,
              Discount: item.discount_amount,
              NetPayableIncludingGST: item.total,
              AdvanceReceived: item.paid_amount,
              AdvancePaymentMode: "TODO",
              AdvancePaid: "TODO",
              BalancePaymentMode: "TODO",
              AdonBills: "TODO",
              AdonPaymentMode: item.payment_mode,
              AdditionalGuests: item.no_of_adults_additional,
              TaxableAmount5Percent: "TODO",
              TaxableAmount12Percent: "TODO",
              TaxableAmount18Percent: "TODO",
              GST5Percent: item.gst_5,
              GST12Percent: "TODO", 
              GST18Percent: item.gst_18,

          };
      });

      console.log(guestBookingListCSV);

    exportFromJSON({
        data: guestBookingListCSV,
      fileName: `Guest-List-${today}`,
      exportType: exportFromJSON.types.csv,
    });
  };
  return (
    <>
      <PageTitle>{t("Guests")}</PageTitle>
      <MainDrawer>
        <ProductDrawer id={serviceId} />
      </MainDrawer>
      <MainDrawer>
        <AddOnBillDrawer
          id={serviceId}
          getAllBookingList={getAllBookingList}
          pagenumber={pagenumber}
          searchname={searchname}
          StartDate={StartDate}
          EndDate={EndDate}
          expo={expo}
          bookingUser={bookingUser}
          bookingId={bookingId}
        />
      </MainDrawer>
      <Card className="min-w-0 shadow-xs overflow-hidden bg-white dark:bg-gray-800 mb-5">
        <CardBody>
          <form
            onSubmit={handleSubmitForAll}
            className="py-3 md:pb-0 grid gap-6 lg:gap-6 xl:gap-6  xl:flex"
          >
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                {"name/email/phone"}
              </label>
              <Input
                ref={userRef}
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="search"
                onChange={(e) => handleSearch(e.target.value)}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
              <button
                type="submit"
                className="absolute right-0 top-0 mt-5 mr-1"
              ></button>
            </div>
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                {t("start date")}
              </label>
              <Input
                ref={userRef}
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="date"
                onChange={setDateFillter}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
            </div>
            <div className="flex-grow-0 md:flex-grow lg:flex-grow xl:flex-grow">
              <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                {t("end date")}
              </label>
              <Input
                ref={userRef}
                className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                type="date"
                onChange={setEndDateFillter}
                name="search"
                placeholder={t("StaffSearchBy")}
              />
            </div>
            <div className="w-full md:w-40 lg:w-40 xl:w-40 mr-3 mb-3 lg:mb-0">
              <Button
                type="button"
                style={{ height: "47px" }}
                onClick={handleExportCSV}
                className="focus:outline-none"
              >
                <span className="flex items-center text-sm">
                  <BsFileEarmarkMedical
                    className="w-4 h-4 mr-3"
                    aria-hidden="true"
                  />

                  <span>Export to CSV</span>
                </span>
              </Button>
            </div>
            <div className="">
              <div className="w-full md:w-48 lg:w-48 xl:w-48">
                <Link to="/add-guest">
                  {" "}
                  <Button className="w-full rounded-md h-12">
                    <span className="mr-2">
                      <FiPlus />
                    </span>
                    {t("Add Guests")}
                  </Button>
                </Link>
              </div>
            </div>
          </form>
        </CardBody>
      </Card>

      {loading ? (
        <TableLoading row={12} col={7} width={160} height={20} />
      ) : serviceData?.length !== 0 ? (
        <TableContainer className="mb-8 rounded-b-lg">
          <Table>
            <TableHeader>
              <tr>
                <TableCell>{"S.no"}</TableCell>
                <TableCell>{t("Property Details")}</TableCell>
                <TableCell>{t("Package Details")}</TableCell>
                <TableCell>{t("Event Details")}</TableCell>
                <TableCell>{t("Booking Details")}</TableCell>
                <TableCell>{t("Guest Details")}</TableCell>
                <TableCell className="">{t("Food Details")}</TableCell>
                <TableCell className="">{t("Check In Details")}</TableCell>
                <TableCell className="">{t("Check Out Details")}</TableCell>
                <TableCell>{t("Items")}</TableCell>
                <TableCell className="">{t("Payment Details")}</TableCell>
                <TableCell className="">{t("Status")}</TableCell>
                <TableCell className="text-right">
                  {t("Guest Status")}
                </TableCell>
                <TableCell className="text-center">{t("Action")}</TableCell>
              </tr>
            </TableHeader>
            <ProductTable
              lang={lang}
              // isCheck={isCheck}
              pagenumber={pagenumber}
              searchname={searchname}
              StartDate={StartDate}
              EndDate={EndDate}
              expo={expo}
              getAllBookingList={getAllBookingList}
              GuestsList={BookingList?.Bookings}
              wholeCount={wholeCount}
              handleUpdate={handleUpdate}
              handleAdd={handleAdd}
              // setIsCheck={setIsCheck}
            />
          </Table>
          <TableFooter>
            <Pagination
              totalResults={BookingList?.pagination?.total}
              resultsPerPage={BookingList?.pagination?.pageSize}
              onChange={handleChangePagenumber}
              label="Product Page Navigation"
            />
          </TableFooter>
        </TableContainer>
      ) : (
        <NotFound title="Product" />
      )}
    </>
  );
};

export default Products;
