import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { SidebarContext } from "context/SidebarContext";
import { notifyError, notifySuccess } from "utils/toast";
import { createGuestStatus } from "services/Api";
import { useHistory } from "react-router-dom";
import moment from "moment";

const useFAQsSubmit = (
  getAllBookingList,
  pagenumber,
  searchname,
  StartDate,
  EndDate,
  expo
) => {
  const { setIsUpdate, closeDrawer, closeModal } = useContext(SidebarContext);
  const [isSave, setIsSave] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [imageUrl, setImageUrl] = useState([]);
  const [serviceId, setserviceId] = useState("");
  const [additionalGuestAmount, setAdditionalGuestAmount] = useState(0);
  const [additionalGuestPaidAmount, setAdditionalGuestPaidAmount] = useState(0);
  const history = useHistory();
  const {
    register,
    handleSubmit,
    reset,
    setValue,
    watch,
    formState: { errors },
  } = useForm();

  const updateValidation = (adultValue, childValue) => {
    const fieldValidation =
      typeof adultValue === "string" &&
      adultValue.length === 0 &&
      typeof childValue === "string" &&
      childValue.length === 0
        ? { required: "" }
        : { required: "Amount Require for additional guest" };

    register("AmountForAdditionGuests", fieldValidation);
  };

  const addition_audult_guest_checkinFieldValue = watch(
    "NoofAdditionalAdultCheckin"
  );
  const addition_child_guest_checkinFieldValue = watch(
    "NoofAdditionalChildCheckin"
  );

  updateValidation(
    addition_audult_guest_checkinFieldValue,
    addition_child_guest_checkinFieldValue
  );

  const updateAmountForAdditionGuest = (value) => {
    setAdditionalGuestAmount(parseInt(value));
  };

  useEffect(() => {
    const amount = watch("AmountForAdditionGuests");
    if (typeof amount === "string" && amount.length !== 0) {
      updateAmountForAdditionGuest(amount);
    } else {
      updateAmountForAdditionGuest(0);
    }
  });

  const updatePaidAmountForAdditionGuest = (value) => {
    setAdditionalGuestPaidAmount(parseInt(value));
  };

  useEffect(() => {
    const amount = watch("PaidAmount");
    if (typeof amount === "string" && amount.length !== 0) {
      updatePaidAmountForAdditionGuest(amount);
    } else {
      updatePaidAmountForAdditionGuest(0);
    }
  });

  const onSubmit = async (data) => {
    // console.log(data, "datadata", serviceId);
    // if (imageUrl.length === 0) {
    //     setTimeout(() => {
    //       setIsSubmitting(false)
    //       }, 2000);
    //     return notifyError("File is empty. Please select a valid file.");
    //   } else {
    console.log(data, "hit");
    const propertyImages = imageUrl.map((item) => ({
      image: typeof item.image === "string" ? item.image : item.image.image,
    }));
    try {
      setIsSubmitting(true);
      const currentTime = moment().format("HH:mm");
      const payload = {
        id: serviceId,
        booking_status: "checked_in",
        // check_in_time: data?.CheckInTime,
        check_in_time: currentTime,
        no_of_adults_checked_in: data?.NoofAdultCheckin,
        no_of_children_checked_in: data?.NoofChildCheckin || 0,
        no_of_additional_adult_during_checkin:
          data.NoofAdditionalAdultCheckin || 0,
        no_of_additional_child_during_checkin:
          data.NoofAdditionalChildCheckin || 0,
        amount_for_additional_guest_during_checkin:
          data.AmountForAdditionGuests || 0,
        additional_checkin_remark: data.Remarks,
        paid_amount_during_checkin: data.PaidAmount || 0,
        payment_mode_on_spot: data.PaymentMode,
        id_proof_type: data?.DocumentType,
        id_doc_url: propertyImages[0]?.image,
        doc_number: data?.DocumentNo,
        room_no: data?.RoomNo,
      };
      let res = {};
      // console.log(payload);
      res = await createGuestStatus(payload);
      if (res.data.status == true) {
        notifySuccess(res.data.message);
        reset();
        closeModal();
        setImageUrl([]);
        setAdditionalGuestAmount(0);
        // setAmountFieldDisabled(true);
        getAllBookingList(pagenumber, searchname, StartDate, EndDate, "false");
      }
      setIsSubmitting(false);
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
    }
    // };
  };

  return {
    errors,
    register,
    handleSubmit,
    onSubmit,
    isSave,
    setValue,
    imageUrl,
    serviceId,
    setserviceId,
    setImageUrl,
    isSubmitting,
    watch,
    additionalGuestAmount,
    additionalGuestPaidAmount,
  };
};

export default useFAQsSubmit;
