import React, { useContext, useEffect, useState } from "react";
// import { useLocation } from "react-router-dom";
// import InputAreaTwo from "components/form/InputAreaTwo";
import {
  Modal,
  ModalBody,
  ModalFooter,
  Button,
  Select,
  Input,
  // Alert,
} from "@windmill/react-ui";
// import { FiTrash2 } from "react-icons/fi";
// import ProductServices from "services/ProductServices";
import { SidebarContext } from "context/SidebarContext";
// import { notifySuccess, notifyError } from "utils/toast";
// import useToggleDrawer from "hooks/useToggleDrawer";
// import { DeleteDayOutPAckages } from "services/Api";
import spinnerLoadingImage from "../../assets/img/spinner.gif";
import useSettingSubmit from "hooks/useCheckinStatus";
import { useTranslation } from "react-i18next";
import Uploader from "components/image-uploader/Uploader";
// import LabelArea from "components/form/LabelArea";
import Error from "components/form/Error";
import PageTitle from "components/Typography/PageTitle";
import { gst } from "utils/constant";
// import DrawerButton from "components/form/DrawerButton";
const MainModal = ({
  mainID,
  getAllBookingList,
  pagenumber,
  searchname,
  StartDate,
  EndDate,
  expo,
  userData,
  setUserData,
  GuestsList,
}) => {
  const { t } = useTranslation();
  const {
    errors,
    register,
    handleSubmit,
    onSubmit,
    imageUrl,
    setImageUrl,
    isSave,
    isSubmitting,
    serviceId,
    setserviceId,
    handleProductTap,
    setId,
    setValue,
    additionalGuestAmount,
    additionalGuestPaidAmount,
  } = useSettingSubmit(
    getAllBookingList,
    pagenumber,
    searchname,
    StartDate,
    EndDate,
    expo
  );
  const { isModalOpen, closeModal, toggleDrawer } = useContext(SidebarContext);
  const [singleGuestData, setSingleGuestData] = useState({});

  useEffect(() => {
    console.log(serviceId, mainID, "datadata");
    setserviceId(mainID);
    setSingleGuestData(GuestsList?.find((guest) => guest.id === mainID));
  }, [mainID]);
  // console.log(imageUrl, "imageUrl");

  // const calculateGST = () => {
  //   const gstOtherTotalPercent = gst.other_cgst + gst.other_sgst
  //   const gstOtherTotal = `${((gstOtherTotalPercent / 100) * (additionalGuestAmount)).toFixed(2)}`

  //   let totalAmount = (parseFloat(gstOtherTotal) + additionalGuestAmount).toFixed(2)
  //   return totalAmount
  // }

  useEffect(() => {
    console.log(userData, "usertest");

    setValue("DocumentType", userData?.id_proof_type);
    setValue("DocumentNo", userData?.doc_number);
    if (userData?.id_doc_url) {
      setImageUrl([{ image: userData?.id_doc_url }]);
    } else {
      setImageUrl([]);
    }
  }, [userData]);

  useEffect(() => {
    setValue("NoofAdultCheckin", singleGuestData?.no_of_adults);
    setValue("NoofChildCheckin", singleGuestData?.no_of_children);
  },[singleGuestData, setValue])

  return (
    <>
      <Modal
        isOpen={isModalOpen}
        style={{
          maxWidth: 1300,
          maxHeight: "-webkit-fill-available",
          overflow: "auto",
        }}
        onClose={() => {
          closeModal();
          setUserData({});
          setImageUrl([]);
        }}
      >
        <ModalBody className="custom-modal px-8 pt-6">
          <PageTitle>{t("Update Check In Details")}</PageTitle>
          <form onSubmit={handleSubmit(onSubmit)} className="block" id="block">
            <div className="col-span-12 md:col-span-12 lg:col-span-12 mr-3 ">
              <div className="flex-grow scrollbar-hide w-full max-h-full pb-0">
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4">
                  {/* <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Check In Time")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("CheckInTime", {
                          required: "Checkin time required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("CheckInTime")}
                        name="CheckInTime"
                        type="time"
                        placeholder={t("Enter CheckInTime")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.CheckInTime} pos />
                    </div>
                  </div> */}
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("No Of Adult Check In")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("NoofAdultCheckin", {
                          required: "No Of Adult Check In required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("NoofAdultCheckin")}
                        name="NoofAdultCheckin"
                        type="text"
                        placeholder={t("Enter No Of Adult Check In")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.NoofAdultCheckin} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("No Of Child Check In")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("NoofChildCheckin", {
                          // required: "No Of Adult Check Out required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("NoofChildCheckin")}
                        name="NoofChildCheckin"
                        type="text"
                        placeholder={t("Enter No Of Child Check In")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.NoofAdultCheckout} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("No of Additional Adult Check in")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("NoofAdditionalAdultCheckin", {
                          // required: "No Of Adult Check Out required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("NoofAdditionalAdultCheckin")}
                        name="NoofAdditionalAdultCheckin"
                        type="text"
                        placeholder={t("No of Additional Adult Check in")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("No of Additional Child Check in")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("NoofAdditionalChildCheckin", {
                          // required: "No Of Adult Check Out required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("NoofAdditionalChildCheckin")}
                        name="NoofAdditionalChildCheckin"
                        type="text"
                        placeholder={t("No of Additional Child Check in")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Amount For Addition Guests (WITH GST)")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("AmountForAdditionGuests", {
                          // required: "Amount Require for additional guest",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("AmountForAdditionGuests")}
                        name="AmountForAdditionGuests"
                        type="text"
                        placeholder={t("Enter amount for addition guests")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.AmountForAdditionGuests} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Remarks")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("Remarks", {
                          // required: "No Of Adult Check Out required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("Remarks")}
                        name="Remarks"
                        type="text"
                        placeholder={t("Enter Remarks")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Document Type")}
                    </label>
                    <div className="sm:col-span-2">
                      <Select
                        {...register(`DocumentType`, {
                          // required: "Select Document Type is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Document Type")}
                        </option>
                        <option value="adhaar">Aadhaar</option>
                        {/* <option value="pan">PAN</option> */}
                        <option value="voter-id">Voter ID</option>
                        <option value="passport">Passport</option>
                        <option value="driving license">Driving license</option>
                      </Select>
                      <Error errorName={errors.BookingStatus} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Document No")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("DocumentNo", {
                          // required: "No Of Adult Check Out required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("DocumentNo")}
                        name="DocumentNo"
                        type="text"
                        placeholder={t("Enter Document No")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.DocumentNo} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Room No")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("RoomNo", {
                          // required: "No Of Adult Check Out required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("RoomNo")}
                        name="RoomNo"
                        type="text"
                        placeholder={t("Enter Room No")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.RoomNo} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Paid Amount")}
                    </label>
                    <div className="sm:col-span-2">
                      <Input
                        {...register("PaidAmount", {
                          // required: "No Of Adult Check Out required!",
                          // validate: (value) => !containsSpecialCharsOrNumbers(value) || 'Name cannot contain special characters or numbers',
                        })}
                        label={t("PaidAmount")}
                        name="PaidAmount"
                        type="text"
                        placeholder={t("Enter Paid Amount")}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      />
                      <Error errorName={errors.PaidAmount} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Payment Mode")}
                    </label>
                    <div className="sm:col-span-2">
                      <Select
                        multiple
                        {...register(`PaymentMode`, {
                          // required: "Select Document Type is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        {/* Cash, UPI, Cards, Payment Link, Account Transfer */}
                        <option value="" defaultValue hidden>
                          {t("Select Payment Mode")}
                        </option>
                        <option value="cash">Cash</option>
                        <option value="upi">UPI</option>
                        <option value="cards">Cards</option>
                        <option value="payment-link">Payment Link</option>
                        <option value="account-transfer">
                          Account Transfer
                        </option>
                      </Select>
                      <Error errorName={errors.PaymentMode} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-6">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Photos")}
                    </label>
                    <div className="">
                      <div>
                        <Uploader
                          product
                          folder="product"
                          imageUrl={imageUrl}
                          setImageUrl={setImageUrl}
                        />
                      </div>
                      <Error errorName={errors.totalAmount} pos />
                    </div>

                    <div className="sm:col-span-3">
                      {singleGuestData && (
                        <div className="mb-4">
                          <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                            Payment Details
                          </label>

                          <ul className="p-3 m-0  w-100 border border-dotted">
                            <li className="mb-2">
                              <div className="flex flex-row justify-between">
                                <div>
                                  <p className="para_head mb-0">
                                    {t("Paid amount")}
                                  </p>
                                </div>

                                <div>
                                  <p className="para_head mb-0 mx-lg-2">
                                    ₹{" "}
                                    {
                                      singleGuestData?.price_summary
                                        ?.booking_paid_amount
                                    }
                                  </p>
                                </div>
                              </div>
                            </li>
                            {additionalGuestAmount !== 0 && (
                              <li className="mb-2">
                                <div className="flex flex-row justify-between">
                                  <div>
                                    <p className="para_head mb-0">
                                      {t("Additional Guest amount (including GST)")}
                                    </p>
                                  </div>

                                  <div>
                                    <p className="para_head mb-0 mx-lg-2">
                                      ₹ {parseFloat(additionalGuestAmount)}
                                    </p>
                                  </div>
                                </div>
                              </li>
                            )}
                            <li className="mb-2">
                              <div className="flex flex-row justify-between">
                                <div>
                                  <p className="para_head mb-0">
                                    {t("Balance amount")}
                                  </p>
                                </div>

                                <div>
                                  {additionalGuestAmount !== 0 ? (
                                    <p className="para_head mb-0 mx-lg-2">
                                      ₹{" "}
                                      {parseInt(
                                        singleGuestData?.price_summary
                                          ?.booking_balance_amount
                                      ) + parseFloat(additionalGuestAmount)}
                                    </p>
                                  ) : (
                                    <p className="para_head mb-0 mx-lg-2">
                                      ₹{" "}
                                      {
                                        singleGuestData?.price_summary
                                          ?.booking_balance_amount
                                      }
                                    </p>
                                  )}
                                </div>
                              </div>
                            </li>
                            <hr class="border-t border-dotted" />
                            <div className="flex flex-row justify-between  ">
                              <strong className="reviewCard_title">
                                Total (including GST)
                              </strong>
                              {additionalGuestAmount !== 0 ? (
                                <strong className="reviewCard_title">
                                  ₹{" "}
                                  {parseInt(
                                    singleGuestData?.price_summary
                                      ?.booking_total_amount
                                  ) + parseFloat(additionalGuestAmount)}
                                </strong>
                              ) : (
                                <strong className="reviewCard_title">
                                  ₹{" "}
                                  {
                                    singleGuestData?.price_summary
                                      ?.booking_total_amount
                                  }
                                </strong>
                              )}
                            </div>
                          </ul>
                        </div>
                      )}
                    </div>

                    <div className="flex flex-row-reverse gap-3 pb-6">
                      {isSubmitting ? (
                        <Button disabled={true} type="button" className="h-12">
                          <img
                            src={spinnerLoadingImage}
                            alt="Loading"
                            width={20}
                            height={10}
                          />{" "}
                          <span className="font-serif ml-2 font-light">
                            Processing
                          </span>
                        </Button>
                      ) : (
                        <Button type="submit" className="h-12 px-8">
                          {" "}
                          {isSave ? "Save" : "Update"}
                        </Button>
                      )}
                      <Button
                        onClick={() => closeModal()}
                        style={{
                          backgroundColor: "gainsboro",
                          color: "red",
                          position: "relative",
                        }}
                      >
                        Cancel
                      </Button>{" "}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </ModalBody>
        <ModalFooter className="justify-center"></ModalFooter>
      </Modal>
    </>
  );
};

export default React.memo(MainModal);
