import React, { useContext, useEffect, useState } from "react";

import {
  Modal,
  ModalBody,
  Button,
  TableContainer,
  Table,
  TableHeader,
  TableCell,
  TableBody,
  TableRow,
  Select,
} from "@windmill/react-ui";

import { SidebarContext } from "context/SidebarContext";
import spinnerLoadingImage from "../../assets/img/spinner.gif";
import useCheckOutStatus from "hooks/useCheckOutStatus";
import { useTranslation } from "react-i18next";
import Error from "components/form/Error";
import PageTitle from "components/Typography/PageTitle";
import "react-datepicker/dist/react-datepicker.css";
const MainModal = ({
  mainID,
  getAllBookingList,
  pagenumber,
  searchname,
  StartDate,
  EndDate,
  expo,
  userData,
  setUserData,
}) => {
  const { t } = useTranslation();
  const {
    errors,
    register,
    handleSubmit,
    onSubmit,
    imageUrl,
    setImageUrl,
    isSave,
    isSubmitting,
    serviceId,
    setserviceId,
    handleProductTap,
    setId,
    setValue,
  } = useCheckOutStatus(
    getAllBookingList,
    pagenumber,
    searchname,
    StartDate,
    EndDate,
    expo
  );
  const { isModalOpen2, closeModal2, toggleDrawer } =
    useContext(SidebarContext);
  const [checkIn, setCheckIn] = useState("");
  const [checkOut, setCheckOut] = useState("");

  // const [checkoutProcessed, setCheckoutProcessed] = useState(false);

  useEffect(() => {
    if (userData?.id) {
      setserviceId(userData?.id);
    }
  }, [setserviceId, userData?.id]);

  useEffect(() => {
    console.log(userData, "userData");
    if (userData?.check_in_date) {
      setCheckIn(new Date(userData?.check_in_date));
    }
    if (userData?.check_out_date) {
      setCheckOut(new Date(userData?.check_out_date));
    }
  }, [userData]);

  console.log(checkIn, checkOut, "checkOut");

  return (
    <>
      <Modal
        isOpen={isModalOpen2}
        style={{ maxWidth: 700 }}
        onClose={() => {
          closeModal2();
          setUserData({});
        }}
      >
        <ModalBody>
          <PageTitle>{t("Confirm Check-Out")}</PageTitle>
          <form onSubmit={handleSubmit(onSubmit)} className="block" id="block">
            <div className="col-span-12 md:col-span-12 lg:col-span-12 mr-3 ">
              <div className="flex-grow scrollbar-hide w-full max-h-full pb-0">
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4">
                  <div className="sm:col-span-6">
                    {/* <div className="mb-4"> */}
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      Payment Details
                    </label>

                    <TableContainer className="rounded-b-lg">
                      <Table>
                        <TableHeader>
                          <tr>
                            <TableCell>{t("Title")}</TableCell>
                            <TableCell>{t("Total")}</TableCell>
                            <TableCell>{t("Paid Amount")}</TableCell>
                            <TableCell>{t("Pending Amount")}</TableCell>
                          </tr>
                        </TableHeader>
                        <TableBody>
                          <TableRow>
                            <TableCell>
                              <span className="text-sm">
                                {userData?.Booking_package
                                  ? userData?.Booking_package?.name
                                  : userData?.Booking_event
                                  ? userData?.Booking_event?.name
                                  : "Package"}
                              </span>
                            </TableCell>

                            <TableCell>
                              <span className="text-sm">
                                {userData?.price_summary?.booking_total_amount}
                              </span>
                            </TableCell>
                            <TableCell>
                              <span className="text-sm">
                                {userData?.price_summary?.booking_paid_amount}
                              </span>
                            </TableCell>
                            <TableCell>
                              <span className="text-sm">
                                {
                                  userData?.price_summary
                                    ?.booking_balance_amount
                                }
                              </span>
                            </TableCell>
                          </TableRow>
                          {userData?.price_summary
                            ?.booking_addon_total_amount && (
                            <TableRow>
                              <TableCell>
                                <span className="text-sm">Add-On</span>
                              </TableCell>

                              <TableCell>
                                <span className="text-sm">
                                  {
                                    userData?.price_summary
                                      ?.booking_addon_total_amount
                                  }
                                </span>
                              </TableCell>
                              <TableCell>
                                <span className="text-sm">0</span>
                              </TableCell>
                              <TableCell>
                                <span className="text-sm">
                                  {
                                    userData?.price_summary
                                      ?.booking_addon_total_amount
                                  }
                                </span>
                              </TableCell>
                            </TableRow>
                          )}
                        </TableBody>
                      </Table>
                    </TableContainer>
                  </div>

                  <div className="sm:col-span-6">
                    {/* {checkoutProcessed && ( */}
                    <>
                      <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                        {t("Payment Mode")}
                      </label>
                      <div className="sm:col-span-6 mb-8">
                        <Select
                          multiple
                          {...register(`PaymentMode`, {
                            // required: "Select Any Payment Mode",
                          })}
                          className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                        >
                          {/* Cash, UPI, Cards, Payment Link, Account Transfer */}
                          <option value="" defaultValue hidden>
                            {t("Select Payment Mode")}
                          </option>
                          <option value="cash">Cash</option>
                          <option value="upi">UPI</option>
                          <option value="cards">Cards</option>
                          <option value="payment-link">Payment Link</option>
                          <option value="account-transfer">
                            Account Transfer
                          </option>
                        </Select>
                        <Error errorName={errors.PaymentMode} pos />
                      </div>
                    </>
                    {/* )} */}

                    {/* {checkoutProcessed ? (
                      <div className="flex flex-row-reverse gap-3 pb-6">
                        {isSubmitting ? (
                          <Button
                            disabled={true}
                            type="button"
                            className="h-12"
                          >
                            <img
                              src={spinnerLoadingImage}
                              alt="Loading"
                              width={20}
                              height={10}
                            />{" "}
                            <span className="font-serif ml-2 font-light">
                              Processing
                            </span>
                          </Button>
                        ) : (
                          <Button type="submit" className="h-12 px-8">
                            {" "}
                            {isSave ? "Confirm Check-out" : "Update"}
                          </Button>
                        )}
                        <Button
                          onClick={() => closeModal2()}
                          style={{
                            backgroundColor: "gainsboro",
                            color: "red",
                            position: "relative",
                          }}
                        >
                          Cancel
                        </Button>{" "}
                      </div>
                    ) : (
                      <div className="flex flex-row-reverse gap-3 pb-6">
                        <Button
                          type="button"
                          onClick={() => setCheckoutProcessed(true)}
                          className="h-12"
                        >
                          Processed with Check-out
                        </Button>
                        <Button
                          onClick={() => closeModal2()}
                          style={{
                            backgroundColor: "gainsboro",
                            color: "red",
                            position: "relative",
                          }}
                        >
                          Cancel
                        </Button>{" "}
                      </div>
                    )} */}

                    <div className="flex flex-row-reverse gap-3 pb-6">
                      {isSubmitting ? (
                        <Button disabled={true} type="button" className="h-12">
                          <img
                            src={spinnerLoadingImage}
                            alt="Loading"
                            width={20}
                            height={10}
                          />{" "}
                          <span className="font-serif ml-2 font-light">
                            Processing
                          </span>
                        </Button>
                      ) : (
                        <Button type="submit" className="h-12 px-8">
                          {" "}
                          {isSave ? "Confirm Check-out" : "Update"}
                        </Button>
                      )}
                      <Button
                        onClick={() => closeModal2()}
                        style={{
                          backgroundColor: "gainsboro",
                          color: "red",
                          position: "relative",
                        }}
                      >
                        Cancel
                      </Button>{" "}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </ModalBody>
        {/* <ModalFooter className="justify-center">
          <Button
            onClick={() => closeModal2()}
            style={{
              backgroundColor: "gainsboro",
              color: "red",
              position: "relative",
            }}
          >
            Cancel
          </Button>{" "}
          <Button onClick={checkoutHandler}>Confirm Check-out</Button>
        </ModalFooter> */}
      </Modal>
    </>
  );
};

export default React.memo(MainModal);
