import {
  // Avatar,
  Badge,
  TableBody,
  TableCell,
  TableRow,
  Button,
} from "@windmill/react-ui";
import DeleteModal from "./CheckinGuestDetails";
import RescheduleModal from "./rescheduleBooking";
import CheckOutModal from "./checkoutconfirm";
import Tooltip from "components/tooltip/Tooltip";
import useToggleDrawer from "hooks/useToggleDrawer";
import { t } from "i18next";
import { useContext, useState } from "react";
import {
  // FiCheck,
  // FiEdit,
  FiEye,
  // FiShoppingCart,
  // FiTrash2,
  // FiZoomIn,
} from "react-icons/fi";
import { useHistory, useLocation } from "react-router-dom";
import moment from "moment";
import { SidebarContext } from "context/SidebarContext";
import { notifyError, notifySuccess } from "utils/toast";
import { createGuestStatus } from "services/Api";
import { Link } from "react-router-dom";

const ProductTable = ({
  GuestsList,
  serviceId,
  setserviceId,
  getAllBookingList,
  setIsCheck,
  wholeCount,
  pagenumber,
  searchname,
  StartDate,
  EndDate,
  expo,
  lang,
  handleUpdate,
  handleAdd,
}) => {
  const { title } = useToggleDrawer();
  const history = useHistory();
  const [userData, setUserData] = useState({});
  const { toggleDrawer, toggleModal, toggleModal1, toggleModal2 } =
    useContext(SidebarContext);
  // const {
  //   toggleDrawer,
  //   lang,
  //   currentPage,
  //   // handleChangePage,
  //   searchText,
  //   category,
  //   setCategory,
  //   searchRef,
  //   handleSubmitForAll,
  //   sortedField,
  //   setSortedField,
  //   limitData,
  //   isUpdated,
  // } = useContext(SidebarContext);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const location = useLocation();
  const [mainID, setmainID] = useState("");
  const handleUpdate1 = (id) => {
    console.log("clicked");
    history.replace(`/edit-guest/${id}`);
  };
  const UpdateGuestsDetails = () => {
    history.replace("/guest-details");
  };

  const handleModalOpen = (id) => {
    toggleModal();
    setmainID(id);
  };

  const handleModalOpen2 = (data) => {
    toggleModal1();
    setUserData(data);
  };

  const handleModalOpen3 = (data) => {
    toggleModal2();
    setUserData(data);
  };
  const handleModalOpen1 = async (data) => {
    const currentTime = moment().format("HH:mm");
    // const currentTime = new Date();
    // const options = { hour: '2-digit', minute: '2-digit', second: '2-digit' };
    // const currentTimeString = currentTime.toLocaleTimeString('en-US', options);
    console.log("Current Time:", currentTime);
    try {
      setIsSubmitting(true);
      const payload = {
        booking_status: "checked_out",
        id: data,
        check_out_time: currentTime,
      };
      let res = {};
      res = await createGuestStatus(payload);
      if (res.data.status == true) {
        notifySuccess(res.data.message);
        getAllBookingList(pagenumber, searchname, StartDate, EndDate, "false");
      }
      setIsSubmitting(false);
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
    }
  };
  console.log(GuestsList, "GuestsList");

  return (
    <>
      <DeleteModal
        userData={userData}
        setUserData={setUserData}
        getAllBookingList={getAllBookingList}
        serviceId={serviceId}
        mainID={mainID}
        title={title}
        pagenumber={pagenumber}
        searchname={searchname}
        StartDate={StartDate}
        EndDate={EndDate}
        expo={expo}
        GuestsList={GuestsList}
      />
      <RescheduleModal
        userData={userData}
        setUserData={setUserData}
        getAllBookingList={getAllBookingList}
        serviceId={serviceId}
        mainID={mainID}
        title={title}
        pagenumber={pagenumber}
        searchname={searchname}
        StartDate={StartDate}
        EndDate={EndDate}
        expo={expo}
      />
      <CheckOutModal
        userData={userData}
        setUserData={setUserData}
        getAllBookingList={getAllBookingList}
        serviceId={serviceId}
        mainID={mainID}
        title={title}
        pagenumber={pagenumber}
        searchname={searchname}
        StartDate={StartDate}
        EndDate={EndDate}
        expo={expo}
      />
      <TableBody>
        {GuestsList?.map((Guests, i) => (
          <TableRow key={i + 1}>
            <TableCell>
              <span className="text-sm">
                {(wholeCount?.currentPage - 1) * wholeCount?.pageSize + i + 1}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">{Guests?.Booking_property?.name}</span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">
                {Guests?.Booking_package?.name || "--"}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm ">
                {Guests?.Booking_event?.name || "--"}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm">{Guests?.User?.name}</span> <br />
              <span className="text-sm">{Guests?.User?.mobile}</span>
              <br />
            </TableCell>
            <TableCell className="">
              <span className="text-sm">
                {"Adult" + " : " + Guests.no_of_adults}
              </span>
              <br />
              <span className="text-sm">
                {"Child" + " : " + Guests?.no_of_children}
              </span>
            </TableCell>

            <TableCell className="">
              <span className="text-sm">
                {"Veg" + " : " + Guests.no_of_veg}
              </span>
              <br />
              <span className="text-sm">
                {"Non Veg" + " : " + Guests?.no_of_nonveg}
              </span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm  ">
                {moment(Guests?.check_in_date).format("YYYY-MM-DD")}
              </span>
              <br />
              <span className="text-sm">
                {"Adult" + " : " + Guests.no_of_adults_checked_in}
              </span>
              <br />
              <span className="text-sm">
                {"Child" + " : " + Guests?.no_of_children_checked_in}
              </span>
              <br />
              <span className="text-sm  ">{Guests?.check_in_time}</span>
              <br />
              <span></span>
            </TableCell>
            <TableCell className="">
              <span className="text-sm  ">
                {moment(Guests?.check_out_date).format("YYYY-MM-DD")}
              </span>
              <br />
              {/* <span className="text-sm">
                {"Adult" + " : " + Guests.no_of_adults_checked_out}
              </span>
              <br />
              <span className="text-sm">
                {"Child" + " : " + Guests?.no_of_children_checked_out}
              </span> */}
              <span className="text-sm  ">{Guests?.check_out_time}</span>
            </TableCell>
            <TableCell className="">
              {Guests?.add_on_bills ? (
                <span className="text-sm">
                  {Guests?.add_on_bills?.Add_on_bill_ala_cartes.length > 0 && (
                    <>
                      {" "}
                      ala-carte:-
                      {Guests?.add_on_bills?.Add_on_bill_ala_cartes?.map(
                        (item) => item?.name + ", "
                      )}
                      <br />
                    </>
                  )}
                  {Guests?.add_on_bills?.Add_on_bill_activities?.length > 0 && (
                    <>
                      {" "}
                      Activities:-
                      {Guests?.add_on_bills?.Add_on_bill_activities?.map(
                        (item) => item?.name + ", "
                      )}
                      <br />
                    </>
                  )}
                  <>Others:-{Guests?.add_on_bills?.others_description}</>
                </span>
              ) : (
                <span className="text-sm">--</span>
              )}
            </TableCell>
            <TableCell>
              <span className="text-sm">{"Total" + " : " + Guests?.total}</span>
              <br />
              <span className="text-sm">
                {"Paid" + " : " + Guests.paid_amount}
              </span>
              <br />
              <span className="text-sm">
                {"Balance" + " : " + Guests.balance_amount}
              </span>
              <br />
              {Guests?.price_summary?.booking_addon_total_amount && (
                <span className="text-sm">
                  {"Add On Bills" +
                    " : " +
                    Guests?.price_summary?.booking_addon_total_amount}
                </span>
              )}
            </TableCell>
            <TableCell>
              <Badge type="success">{Guests?.status}</Badge>
            </TableCell>
            <TableCell className="text-center ">
              <TableCell>
                <Badge type="success">{Guests?.booking_status}</Badge>
              </TableCell>
            </TableCell>
            <TableCell>
              <div className="flex justify-between items-center">
                <Button
                  disabled={
                    Guests?.booking_status === "checked_out" ||
                    Guests?.total_checked_in == true ||
                    Guests?.status === "cancelled"
                  }
                  onClick={() => {
                    setUserData(Guests?.User);
                    handleModalOpen(Guests?.id);
                  }}
                  size="small"
                >
                  Check in
                </Button>
                <Button
                  disabled={
                    Guests?.booking_status === "pending" ||
                    Guests?.booking_status === "checked_out" ||
                    Guests?.status === "cancelled"
                  }
                  className="ml-3"
                  onClick={() => handleModalOpen3(Guests)}
                  size="small"
                >
                  {Guests?.booking_status === "checked_out"
                    ? "Check Out Completed"
                    : "Check Out"}
                </Button>
                <Button
                  disabled={
                    Guests?.booking_status !== "pending" ||
                    Guests?.category === "event" ||
                    Guests?.status === "cancelled"
                  }
                  className="ml-3"
                  onClick={() => handleModalOpen2(Guests)}
                  size="small"
                >
                  Reschedule Booking
                </Button>
                {Guests?.booking_status === "checked_in" &&
                !Guests?.add_on_bills ? (
                  <Button
                    disabled={
                      Guests?.booking_status === "pending" ||
                      Guests?.booking_status === "checked_out" ||
                      Guests?.status === "cancelled"
                    }
                    className="ml-3"
                    onClick={() => handleAdd(Guests?.User, Guests?.id)}
                    size="small"
                  >
                    Add On Bills
                  </Button>
                ) : Guests?.add_on_bills ? (
                  <Button
                    disabled={
                      Guests?.booking_status === "pending" ||
                      Guests?.booking_status === "checked_out" ||
                      Guests?.status === "cancelled"
                    }
                    onClick={() =>
                      handleUpdate(Guests?.add_on_bills?.id, Guests?.id)
                    }
                    className="ml-3"
                    size="small"
                  >
                    Update Add On Bills
                  </Button>
                ) : null}
                <span className="p-2 cursor-pointer text-gray-400 hover:text-green-600">
                  {Guests?.booking_status === "checked_out" && (
                    <Link to={`/guest-invoice/${Guests?.id}`}>
                      <Tooltip
                        id="view"
                        Icon={FiEye}
                        title={t("Invoice View")}
                        bgColor="#059669"
                      />
                    </Link>
                  )}
                </span>
                {/* <button
                  onClick={() => handleUpdate1(Guests?.id)}
                  className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
                >
                  <Tooltip
                    id="Add More Details"
                    Icon={CiCircleInfo}
                    title={t("View Booking")}
                    bgColor="#10B981"
                  />
                </button> */}
              </div>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </>
  );
};

export default ProductTable;
