import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { SidebarContext } from "context/SidebarContext";
import { notifyError, notifySuccess } from "utils/toast";
import { createGuestStatus } from "services/Api";
import moment from "moment";

const useCheckOutStatus = (
  getAllBookingList,
  pagenumber,
  searchname,
  StartDate,
  EndDate,
  expo
) => {
  const { setIsUpdate, closeDrawer, closeModal2 } = useContext(SidebarContext);
  const [isSave, setIsSave] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [imageUrl, setImageUrl] = useState([]);
  const [serviceId, setserviceId] = useState("");
  const {
    register,
    handleSubmit,
    reset,
    setValue,
    watch,
    formState: { errors },
  } = useForm();

  const onSubmit = async (data) => {
    console.log(data, "hit");
    try {
      setIsSubmitting(true);
      const currentTime = moment().format("HH:mm");
      const payload = {
        booking_status: "checked_out",
        id: serviceId,
        check_out_time: currentTime,
        check_out_payment_mode: data.PaymentMode || [],
      };
      let res = {};
      // console.log(payload);
      res = await createGuestStatus(payload);
      if (res.data.status == true) {
        notifySuccess(res.data.message);
        reset();
        closeModal2();
        getAllBookingList(pagenumber, searchname, StartDate, EndDate, "false");
      }
      setIsSubmitting(false);
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
    }
    // };
  };

  return {
    errors,
    register,
    handleSubmit,
    onSubmit,
    isSave,
    setValue,
    imageUrl,
    serviceId,
    setserviceId,
    setImageUrl,
    isSubmitting,
    setValue,
    watch,
  };
};

export default useCheckOutStatus;
