import React, { useContext, useEffect, useState } from "react";

import { Modal, ModalBody, ModalFooter, Button } from "@windmill/react-ui";

import { SidebarContext } from "context/SidebarContext";
import { notifySuccess, notifyError } from "utils/toast";
import useToggleDrawer from "hooks/useToggleDrawer";
import { DeleteDayOutPAckages, rescheduleBookingApi } from "services/Api";
import spinnerLoadingImage from "../../assets/img/spinner.gif";
import useSettingSubmit from "hooks/useCheckinStatus";
import { useTranslation } from "react-i18next";
import Uploader from "components/image-uploader/Uploader";
import LabelArea from "components/form/LabelArea";
import Error from "components/form/Error";
import PageTitle from "components/Typography/PageTitle";
import DrawerButton from "components/form/DrawerButton";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";
const MainModal = ({
  mainID,
  getAllBookingList,
  pagenumber,
  searchname,
  StartDate,
  EndDate,
  expo,
  userData,
  setUserData,
}) => {
  const { t } = useTranslation();
  const {
    errors,
    register,
    handleSubmit,
    onSubmit,
    imageUrl,
    setImageUrl,
    isSave,
    isSubmitting,
    serviceId,
    setserviceId,
    handleProductTap,
    setId,
    setValue,
  } = useSettingSubmit(
    getAllBookingList,
    pagenumber,
    searchname,
    StartDate,
    EndDate,
    expo
  );
  const { isModalOpen1, closeModal1, toggleDrawer } =
    useContext(SidebarContext);
  const [checkIn, setCheckIn] = useState("");
  const [checkOut, setCheckOut] = useState("");

  useEffect(() => {
    console.log(serviceId, mainID, "datadata");
    setserviceId(mainID);
  }, [mainID]);
  console.log(imageUrl, "imageUrl");

  useEffect(() => {
    console.log(userData, "userData");
    if (userData?.check_in_date) {
      setCheckIn(new Date(userData?.check_in_date));
    }
    if (userData?.check_out_date) {
      setCheckOut(new Date(userData?.check_out_date));
    }
  }, [userData]);

  const rescheduleHandler = async (e) => {
    e.preventDefault();
    try {
      const payload = {
        id: userData?.id,
        check_in_date: checkIn,
        check_out_date: checkOut,
      };
      const res = await rescheduleBookingApi(payload);
      if (res.data.status == true) {
        notifySuccess(res.data.message);
        closeModal1();
        getAllBookingList(pagenumber, searchname, StartDate, EndDate, "false");
      }
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
    }
  };

  const checkoutHandler = (date) => {
    setCheckIn(date);
    console.log(userData, "userData");

    if (userData.category === "dayout" && date) {
      setCheckOut(date);
    }
    if (userData.category === "stay" && date) {
      let currentDate = new Date(date);

      // Increase the date by one day
      currentDate.setDate(currentDate.getDate() + 1);
      setCheckOut(currentDate);
    }
  };

  return (
    <>
      <Modal
        isOpen={isModalOpen1}
        onClose={() => {
          closeModal1();
          setUserData({});
        }}
      >
        <ModalBody className="">
          <PageTitle>{t("Reschedule Booking")}</PageTitle>
          <form onSubmit={rescheduleHandler} id="block">
            <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
              <div className="sm:col-span-3">
                <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                  {t("Check In date")}
                </label>
                <div className="sm:col-span-3">
                  <DatePicker
                    minDate={new Date()}
                    placeholder="Please Enter Start Date"
                    calendarPosition="bottom-center"
                    name="StartDate"
                    className="ddd"
                    required
                    selected={checkIn} // Use the "selected" prop to set the value
                    onChange={checkoutHandler} // Handle date changes with this function
                    dateFormat="dd-MM-yyyy"
                  />
                  <Error errorName={errors.CheckInTime} pos />
                </div>
              </div>
            </div>
            <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
              <div className="sm:col-span-3">
                <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                  {t("Check Out Date")}
                </label>
                <div className="sm:col-span-3">
                  <DatePicker
                    minDate={checkIn}
                    disabled={userData?.category === "dayout"}
                    placeholder="Please Enter Start Date"
                    calendarPosition="bottom-center"
                    name="StartDate"
                    className="ddd"
                    required
                    selected={checkOut} // Use the "selected" prop to set the value
                    onChange={setCheckOut} // Handle date changes with this function
                    dateFormat="dd-MM-yyyy"
                  />
                  <Error errorName={errors.CheckInTime} pos />
                </div>
              </div>
            </div>
            <div className="flex flex-row gap-2 justify-center">
              <Button
                onClick={() => closeModal1()}
                style={{
                  backgroundColor: "gainsboro",
                  color: "red",
                  position: "relative",
                }}
              >
                Cancel
              </Button>{" "}
              {isSubmitting ? (
                <Button disabled={true} type="button" className="h-12">
                  <img
                    src={spinnerLoadingImage}
                    alt="Loading"
                    width={20}
                    height={10}
                  />{" "}
                  <span className="font-serif ml-2 font-light">Processing</span>
                </Button>
              ) : (
                <Button type="submit" className="h-12 px-8">
                  {" "}
                  {isSave ? "Save" : "Update"}
                </Button>
              )}
            </div>
          </form>
        </ModalBody>
        <ModalFooter className="justify-center"></ModalFooter>
      </Modal>
    </>
  );
};

export default React.memo(MainModal);
